  background-color: #8a8a8a;
}

.no-webp .choice {
  background-image: url("../img/common/choice.jpeg");
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .no-webp .choice {
    background-image: url("../img/common/choice@2x.jpg");
  }
}

.webp .choice {
  background-image: url("../img/common/choice.webp");
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .webp .choice {
    background-image: url("../img/common/choice@2x.webp");
  }
}

.choice__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.choice__title {
  font-family: "Play", "Arial", sans-serif;
  font-size: 43px;
  line-height: 50px;
  font-weight: 400;
  position: relative;
  letter-spacing: -0.08em;
  text-shadow: 0 0 13px #000000;
  margin-top: 0;
  margin-bottom: 80px;
}
@media (max-width: 959px) {
  .choice__title {
    font-family: "Play", "Arial", sans-serif;
    font-size: 36px;
    line-height: 44px;
    font-weight: 400;
  }
}
@media (max-width: 767px) {
  .choice__title {
    font-family: "Play", "Arial", sans-serif;
    font-size: 28px;
    line-height: 34px;
    font-weight: 400;
  }
}
.choice__title::before {
  content: "";
  position: absolute;
  top: 50%;
  left: -80px;
  height: 2px;
  width: 60px;
  background-color: #ffbf1a;
}
.choice__title::after {
  content: "";
  position: absolute;
  top: 50%;
  right: -80px;
  height: 2px;
  width: 60px;
  background-color: #ffbf1a;
}

.choice__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 40px;
}
@media (max-width: 959px) {
  .choice__list {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.87);
  border-radius: 10px;
  box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.35);
  padding: 45px 20px;
}

.card__title {
  font-family: "Play", "Arial", sans-serif;
  font-size: 30px;
  line-height: 35px;
  font-weight: 400;
  color: #d1d1d1;
  margin-top: 0;
  margin-bottom: 20px;
  text-align: center;
}
@media (max-width: 959px) {
  .card__title {
    font-family: "Play", "Arial", sans-serif;
    font-size: 24px;
    line-height: 28px;
    font-weight: 400;
    letter-spacing: -0.72px;
  }
}
@media (max-width: 767px) {
  .card__title {
    font-family: "Play", "Arial", sans-serif;
    font-size: 22px;
    line-height: 26px;
    font-weight: 400;
  }
}

.card__age {
  font-family: "Play", "Arial", sans-serif;
  font-size: 16px;
  line-height: 19px;
  font-weight: 400;
  color: #d1d1d1;
  margin-top: 0;
}
@media (max-width: 959px) {
  .card__age {
    font-family: "Play", "Arial", sans-serif;
    font-size: 14px;
    line-height: 15px;
    font-weight: 400;
  }
}

.card__description {
  font-family: "Play", "Arial", sans-serif;
  font-size: 23px;
  line-height: 27px;
  font-weight: 400;
  position: relative;
  color: #d1d1d1;
  text-align: center;
  margin-top: 75px;
  margin-bottom: 75px;
}
.card__description::before {
  content: "";
  position: absolute;
  height: 2px;
  width: 143px;
  left: 50%;
  top: -35px;
  transform: translateX(-50%);
  background-color: #ffbf1a;
}
@media (max-width: 767px) {
  .card__description {
    font-family: "Play", "Arial", sans-serif;
    font-size: 16px;
    line-height: 19px;
    font-weight: 400;
    margin-bottom: 45px;
  }
}

.card__price {
  font-family: "Play", "Arial", sans-serif;
  font-size: 35px;
  line-height: 35px;
  font-weight: 400;
  margin-top: auto;
  color: #ffffff;
  margin-bottom: 55px;
}

.card__price span {
  font-family: "Play", "Arial", sans-serif;
  font-size: 16px;
  line-height: 10.88px;
  font-weight: 400;
}

.footer {
  padding-top: 45px;
  padding-bottom: 45px;
}

.footer__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 767px) {
  .footer__nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 25px;
  }
}

.footer__phone {
  font-family: "Play", "Arial", sans-serif;
  font-size: 18px;
  line-height: 21px;
  font-weight: 400;
  transition: opacity 0.3s ease;
}
.footer__phone:hover, .footer__phone:focus, .footer__phone:active {
  opacity: 0.6;
}

.footer-menu {
  display: flex;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  gap: 50px;
  flex-wrap: wrap;
}
@media (max-width: 959px) {
  .footer-menu {
    gap: 25px;
  }
}
@media (max-width: 767px) {
  .footer-menu {
    flex-direction: column;
    gap: 15px;
    align-items: center;
  }
}

.footer-menu__item {
  display: flex;
  align-items: center;
  justify-content: start;
  list-style-type: none;
}

.footer-menu__link {
  font-family: "Righteous", "Arial", sans-serif;
  font-size: 18px;
  line-height: 22px;
  font-weight: 400;
  width: 100%;
  transition: opacity 0.3s ease;
}
.footer-menu__link:hover {
  opacity: 0.6;
}
.footer-menu__link:focus {
  opacity: 0.6;
}
.footer-menu__link:active {
  opacity: 0.6;
}

.btn {
  display: inline-flex;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.btn--outline {
  border: 1px solid #ffffff;
  padding: 9px 15px;
  background-image: none;
}
.btn--outline:hover {
  background-image: none;
  background-color: #ffffff;
  color: #000000;
}
.btn--outline:focus {
  background-image: none;
  background-color: #ffffff;
  color: #000000;
}
.btn--outline:active {
  background-image: none;
  background-color: #ffffff;
  color: #000000;
}
@media (max-width: 767px) {
  .btn--outline {
    justify-content: center;
  }
}

.btn--solid {
  font-family: "Play", "Arial", sans-serif;
  font-size: 16px;
  line-height: 10.88px;
  font-weight: 400;
  background-color: rgba(255, 191, 26, 0.87);
  border: none;
  color: #000000;
  filter: drop-shadow(0 2px 15px #b6b6b6);
  justify-content: center;
  padding: 19px 35px 21px;
}
.btn--solid:hover {
  background-color: #ffff00;
}

.btn--card {
  font-family: "Play", "Arial", sans-serif;
  font-size: 15px;
  line-height: 17px;
  font-weight: 400;
  background-color: #ffff00;
  border: none;
  color: #000000;
  padding-top: 17px;
  padding-bottom: 17px;
  width: 100%;
  justify-content: center;
}
.btn--card:hover {
  background-color: rgba(255, 191, 26, 0.87);
}
.btn--card:focus {
  background-color: rgba(255, 191, 26, 0.87);
}
.btn--card:active {
  background-color: rgba(255, 191, 26, 0.87);
}

