@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin vp1099 {
  @media (max-width: ($vp-1100 - 1px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: ($vp-960 - 1px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: ($vp-768 - 1px)) {
    @content;
  }
}
